@import '../../sample.scss';
@import "../../assets/general-styles";

.wrapper {
  @include background;
  height: 100vh;
}
.wrapper-default{
  @include colorEffectDefault;
}
.wrapper-light{
  @include colorEffectLight;
}
.wrapper-dark{
  @include colorEffectDark;
}
.content{
  margin: auto;
}
.content-dark {
  border-top: 4px #dee4ee solid;
  border-right: 4px #dee4ee solid;
}
.content-light, .content-default{
  border-top: 4px #3362bb solid;
  border-right: 4px #3362bb solid;
}